<template>
    <v-layout
        row
        justify-center
        align-start
    >
        <v-card class="pa-2" color="primary"
            style="border-radius: 15px;"
            width="70vw"
        >
            <v-toolbar color="primary" flat>
                <v-toolbar-side-icon>
                    <v-icon color="white">
                        fas fa-tablet-rugged
                    </v-icon>
                </v-toolbar-side-icon>
                <v-toolbar-title class="white--text">
                    Devices
                </v-toolbar-title>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="gpsMarkers"
                :pagination.sync="pagination"
            >
                <template slot="items" slot-scope="props">
                    <td>{{ props.item.name }}</td>
                    <td>
                        <v-tooltip bottom
                            v-for="(icon, index) in icons"
                            :key="index"
                        >
                            <v-btn
                                icon
                                :to="icon.to"
                                slot="activator"
                                @click="select({
                                    name: props.item.name, 
                                    id: props.item.id, 
                                    type: props.item.species, 
                                    data: props.item.data 
                                })"
                            >
                                <v-icon color="black">
                                    {{ icon.pic }}
                                </v-icon>
                            </v-btn>
                            <span>{{ icon.tooltip }}</span>
                        </v-tooltip>
                    </td>
                    <td>
                      <v-chip
                        :color="props.item.alarm ? 'error' : 'success'"
                      >
                        <v-avatar>
                            <v-icon
                                v-if="props.item.alarm"
                                color="black"
                            >
                                fas fa-exclamation
                            </v-icon>

                             <v-icon
                                v-else
                                color="black"
                            >
                                fas fa-check
                            </v-icon>
                        </v-avatar>
                        <span
                            v-if="props.item.alarm"
                        >
                            In Alert
                        </span>

                         <span
                            v-else
                        >
                            OK
                        </span>

                      </v-chip>

                      <v-chip v-if="props.item.outdated"
                        color="warning"
                      >
                          <v-avatar>
                              <v-icon color="black">
                                  fas fa-clock
                              </v-icon>
                          </v-avatar>
                          <span>
                              Outdated
                          </span>
                      </v-chip>
                    </td>
                    <td>
                        {{ relativeTime(props.item.time) }}
                    </td>
                    <td>
                        {{ props.item.data.batteryV }}
                    </td>


                </template>
            </v-data-table>
        </v-card>
    </v-layout>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import TimeAgo from 'javascript-time-ago';
import en from 'javascript-time-ago/locale/en';

export default {
    data: () => {
        return {
            headers: [
                { text: 'Device Name', value: 'name' },
                { text: 'Actions', value: 'actions', sortable: false },
                { text: 'Reported Asset Status', value: 'status', sortable: false },
                { text: 'Last Received', value: 'time' },
                { text: 'Battery Voltage (mV)', value: 'batteryV' }
            ],
            icons: [
                { tooltip: 'Data History', pic: 'fas fa-chart-line', to: '/details/data' },
                { tooltip: 'Alert History', pic: 'fas fa-exclamation-triangle', to: '/details/alerts' },
                { tooltip: 'Diagnostics', pic: 'fas fa-tools', to: '/details/diag'},
                { tooltip: 'Configurations', pic: 'fas fa-sliders-h', to: '/details/config' },

            ],
            pagination: {
                descending: false,
                rowsPerPage: -1,
                sortBy: 'name'
            }
        }
    },

    methods: {
        ...mapMutations({
            select: 'SELECT_ASSET',
        }),

        relativeTime(timestamp) {
            TimeAgo.addLocale(en);

            const timeAgo = new TimeAgo('en-AU');

            return timeAgo.format(new Date(timestamp));

        }
    },

    computed: {
        ...mapGetters({
            devices: 'GET_DEVICE_NAMES',
        }),
        ...mapState([
            'gpsMarkers'
        ])
    },
}
</script>


<style scoped>
    .v-card {
        margin-top: 60px;
    }
</style>
